.AsideSectionContaner{
    position: fixed;
    left: 0;
    top: 35px;
    width: 24%;
    height: 90%;
}
.AsideSection{
    width: 75%;
    height: 100%;
    background-color: var(--main-color);
    border-radius: 10px;
    margin: auto;
}
.AsideSection .Logo{
    width: 68%;
    height: 50px;
    margin: auto;
}
.AsideSection .Logo img{
    width: 100%;
}
.AsideSection .List {
    width: 100%;
    height: 140px;
    position: relative;
    top: 175px;
}
.AsideSection .List ul {
    width: 100%;
    height: 100%;
    padding: 0;
}
.AsideSection .List ul li {
    list-style: none;
    color: white;
    font-size: 19px;
    font-weight: 500;
    margin: 12px;
    cursor: pointer;
}
.CenterContaner{
    position: absolute;
    right: 0;
    width: 76%;
    top: 35px;
    min-height: 90%;
    height: auto;
}
.CenterSection{
    width: 96%;
    min-height: 90%;
    background-color: white;
    border-radius: 10px;
    position: absolute;
    left: 0;
}